import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div
    style={{
      background: 'url("https://s3.amazonaws.com/kana-assets/bg.jpg")',
      backgroundSize: 'cover',
      height: 'calc(100vh - 8.45rem)'
    }}
  />
)

export default IndexPage



